.app-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 7px 20px;
  h1 {
    color: lightsalmon;
  }
  ul {
    margin-inline-start: 20px;
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  li {
    margin-inline-start: 10px;
  }
  .active-nav {
    font-weight: bold;
    color: red;
  }
}
